import React, { Component } from 'react';
import { Mutation } from 'react-apollo';
import gql from 'graphql-tag';
import { ALL_REQUESTS_QUERY } from './RequestList';

const APPROVE_REQUEST_MUTATION = gql`
  mutation APPROVE_REQUEST_MUTATION($id: ID!) {
    approveRequest(id: $id) {
      id
      approved
    }
  }
`;

class ApproveRequest extends Component {
  update = (cache, payload) => {
    // manually update the cache on the client, so it matches the server
    // 1. Read the cache for the items we want
    const data = cache.readQuery({ query: ALL_REQUESTS_QUERY });
    console.log(data, payload);
    // 2. Filter the deleted itemout of the page
    data.requests = data.requests.filter(request => request.id !== payload.data.approveRequest.id);
    // 3. Put the items back!
    cache.writeQuery({ query: ALL_REQUESTS_QUERY, data });
  };
  render() {
    return (
      <Mutation
        mutation={ APPROVE_REQUEST_MUTATION }
        variables={{ id: this.props.id }}
        update={this.update}
      >
        {(deleteItem, { error }) => (
          <button
            onClick={() => {
              if (confirm('Are you sure you want to approve this request?')) {
                deleteItem().catch(err => {
                  alert(err.message);
                });
              }
            }}
          >
            {this.props.children}
          </button>
        )}
      </Mutation>
    );
  }
}

export default ApproveRequest;
